import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';


const TextCard = (props) => {
  return (
    <div className="TextCard">
      <b>{props.card.name.toString()}</b>
      <p>{props.card.content.map((text) => {return text.toString()})}</p>
    </div>
  );
}

const TextCardList = (props) => {
  return (
    <div className="TextCardList">
      {props.cards.reverse().map((card) => <TextCard {...card}/>)}
    </div>
  );
}

class Form extends Component {
  state =  {
    text: ""
  }

  handleSubmit = (event) => {
    event.preventDefault();
    const http = new XMLHttpRequest();
    const url = "http://127.1.0.0";   // TODO: Make sure that this is right!
    http.onload = () => {
      console.log(http.response);
    }
    http.open("GET", url);
    http.send();
  }

  handleChange = (event) => {
    this.setState({text: event.target.value})
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <input 
          type="text"
          value={this.state.userName}
          onChange={this.handleChange}
          required
        />
        <button type="submit">Enter</button>
      </form>

    );
  }
}

class App extends Component {
  state = {
    cards: [
      {
        name: "Boberto",
        content: [
          "1 banana,",
          "2 banana,",
          "3 banana!"
        ]
      },
      {
        name: "Roberto",
        content: [
          "I really like bananas."
        ]
      }
    ]
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <h1>person_simulator</h1>
          <p>An app designed to simulate people.</p>
        </header>
        <TextCardList cards={this.state.cards}/>
        <Form />
      </div>
    );
  }
} 

export default App;
